import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Container, Row, Col, Card, Button, Tabs, Tab, Spinner, Alert } from 'react-bootstrap';
import api from '../services/api';
import Agents from '../components/Agents';
import TaskList from '../components/TaskList';
import { toast } from 'react-hot-toast';

const TeamDetail = () => {
  const { teamId } = useParams();
  const navigate = useNavigate();
  const [team, setTeam] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [activeTab, setActiveTab] = useState('overview');
  const [logs, setLogs] = useState([]);
  const [retryCount, setRetryCount] = useState(0);
  const maxRetries = 3;

  useEffect(() => {
    fetchTeamDetails();
  }, [teamId, retryCount]);

  const addToLogs = (message, type = 'info') => {
    const timestamp = new Date().toLocaleTimeString();
    const logEntry = {
      id: Date.now(),
      message,
      type,
      timestamp
    };
    setLogs(prev => [...prev, logEntry]);
    console.log(`[${type.toUpperCase()}][${timestamp}] ${message}`);
  };

  const fetchTeamDetails = async () => {
    if (!teamId) {
      setError('Takm ID bilgisi eksik');
      addToLogs('Takm ID bilgisi eksik', 'error');
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      addToLogs(`${teamId} ID'li takm detaylar getiriliyor... (Deneme: ${retryCount + 1}/${maxRetries + 1})`, 'info');
      
      // Takm bilgilerini al
      const response = await api.get(`/api/teams/${teamId}`);
      console.log('Takm yant:', response.data);
      
      if (response.data) {
        const teamData = response.data;
        
        // Eer agents veya tasks alanlar yoksa, bo dizi ile balat
        if (!teamData.agents) teamData.agents = [];
        if (!teamData.tasks) teamData.tasks = [];
        
        setTeam(teamData);
        setError('');
        addToLogs(`Takm bilgileri baaryla alnd: ${teamData.name}`, 'success');
        
        // Takma ait grevleri getir
        try {
          addToLogs(`Takma ait grevler getiriliyor...`, 'info');
          const tasksResponse = await api.get(`/api/teams/${teamId}/tasks`);
          console.log('Grev yant:', tasksResponse.data);
          
          if (tasksResponse.data && tasksResponse.data.tasks) {
            const updatedTeam = {...teamData};
            updatedTeam.tasks = tasksResponse.data.tasks;
            setTeam(updatedTeam);
            addToLogs(`${tasksResponse.data.tasks.length} grev bilgisi alnd`, 'success');
          }
        } catch (err) {
          addToLogs(`Grev bilgileri alnamad: ${err.message}`, 'warning');
          console.error('Grev bilgileri alnrken hata:', err);
        }
      } else {
        throw new Error('Sunucudan veri geldi ancak takm bilgisi bulunamad');
      }
    } catch (error) {
      console.error('Takm detaylar alnrken hata:', error);
      
      // API yant hatas
      if (error.response) {
        const status = error.response.status;
        const errorMessage = error.response.data?.detail || 
                            error.response.data?.message || 
                            `Hata kodu: ${status}`;
        
        setError(`Takm bilgileri yklenemedi: ${errorMessage}`);
        addToLogs(`Hata: ${errorMessage} (${status})`, 'error');
        
        // 404 hatas ise kullancya takmn olmadn bildir
        if (status === 404) {
          toast.error('Takm bulunamad');
        } else {
          toast.error(`Sunucu hatas: ${errorMessage}`);
        }
      } 
      // stek hatas (sunucuya eriilemedi)
      else if (error.request) {
        setError('Sunucuya balanlamad. Ltfen internet balantnz kontrol edin veya daha sonra tekrar deneyin.');
        addToLogs('Sunucuya eriilemedi. Balant hatas.', 'error');
        
        // Otomatik yeniden deneme
        if (retryCount < maxRetries) {
          addToLogs(`${(retryCount + 1) * 2} saniye sonra yeniden denenecek...`, 'warning');
          setTimeout(() => {
            setRetryCount(prev => prev + 1);
          }, (retryCount + 1) * 2000);
        } else {
          toast.error('Sunucuya balanlamad. Ltfen daha sonra tekrar deneyin.');
        }
      } 
      // Dier hatalar
      else {
        setError(`Beklenmeyen bir hata olutu: ${error.message}`);
        addToLogs(`Beklenmeyen hata: ${error.message}`, 'error');
        toast.error('Beklenmeyen bir hata olutu');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleTabChange = (tab) => {
    setActiveTab(tab);
    addToLogs(`Sekme deitirildi: ${tab}`);
  };

  if (loading && retryCount === 0) {
    return (
      <Container className="py-5 text-center">
        <Spinner animation="border" variant="primary" />
        <p className="mt-3">Takm bilgileri ykleniyor...</p>
      </Container>
    );
  }

  if (error && retryCount >= maxRetries) {
    return (
      <Container className="py-5">
        <Alert variant="danger">
          <Alert.Heading>Bir hata olutu</Alert.Heading>
          <p>{error}</p>
          <hr />
          <div className="d-flex justify-content-between align-items-center">
            <Button 
              variant="outline-primary"
              onClick={() => {
                setRetryCount(0);
                fetchTeamDetails();
              }}
            >
              <i className="bi bi-arrow-clockwise me-1"></i> Yeniden Dene
            </Button>
            <Button 
              variant="outline-danger"
              onClick={() => navigate('/teams')}
            >
              <i className="bi bi-arrow-left me-1"></i> Takmlara Dn
            </Button>
          </div>
        </Alert>
        
        {/* Hata ayklama iin loglar gster */}
        <Card className="mt-4">
          <Card.Header>
            <h5 className="mb-0">lem Loglar</h5>
          </Card.Header>
          <Card.Body className="p-0">
            <div className="log-container bg-dark text-light p-3" style={{maxHeight: '300px', overflowY: 'auto'}}>
              {logs.length > 0 ? (
                logs.map(log => (
                  <div key={log.id} className={`log-entry log-${log.type} mb-2`}>
                    <span className="log-time text-muted">[{log.timestamp}]</span>
                    <span className={`log-badge badge bg-${log.type === 'error' ? 'danger' : log.type === 'warning' ? 'warning' : log.type === 'success' ? 'success' : 'info'} mx-2`}>
                      {log.type.toUpperCase()}
                    </span>
                    <span className="log-message">{log.message}</span>
                  </div>
                ))
              ) : (
                <p className="text-muted mb-0">Log kayd bulunmuyor</p>
              )}
            </div>
          </Card.Body>
        </Card>
      </Container>
    );
  }

  if (!team && !loading) {
    return (
      <Container className="py-5">
        <Alert variant="warning">
          <Alert.Heading>Takm bulunamad</Alert.Heading>
          <p>Aradnz takm bulunamad veya eriim izniniz yok.</p>
          <hr />
          <div className="d-flex justify-content-end">
            <Button 
              variant="outline-primary"
              onClick={() => navigate('/teams')}
            >
              Takmlara Dn
            </Button>
          </div>
        </Alert>
      </Container>
    );
  }

  return (
    <Container className="py-4">
      {loading && retryCount > 0 && (
        <Alert variant="info" className="mb-4">
          <div className="d-flex align-items-center">
            <Spinner animation="border" size="sm" className="me-2" />
            <span>Takm bilgileri yeniden ykleniyor... (Deneme: {retryCount + 1}/{maxRetries + 1})</span>
          </div>
        </Alert>
      )}
      
      {error && retryCount < maxRetries && (
        <Alert variant="warning" className="mb-4">
          <div className="d-flex justify-content-between align-items-center">
            <span>{error}</span>
            <Button 
              variant="outline-primary" 
              size="sm"
              onClick={() => {
                setRetryCount(0);
                fetchTeamDetails();
              }}
            >
              <i className="bi bi-arrow-clockwise me-1"></i> imdi Yenile
            </Button>
          </div>
        </Alert>
      )}
      
      {team && (
        <>
          <Card className="mb-4 shadow-sm">
            <Card.Body>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <div>
                  <h1 className="h3 mb-0">{team.name}</h1>
                  <p className="text-muted mb-0">
                    {team.description || 'Takm aklamas bulunmuyor'}
                  </p>
                </div>
                <div className="d-flex">
                  <Button 
                    variant="outline-secondary" 
                    className="me-2"
                    onClick={() => navigate('/teams')}
                  >
                    <i className="bi bi-arrow-left me-1"></i> Takmlar
                  </Button>
                  <Button 
                    variant="primary"
                    onClick={() => {
                      setRetryCount(0);
                      fetchTeamDetails();
                    }}
                  >
                    <i className="bi bi-arrow-clockwise me-1"></i> Yenile
                  </Button>
                </div>
              </div>
              
              <Row className="g-3">
                <Col md={4}>
                  <div className="border rounded p-3 text-center">
                    <h4 className="h6 text-muted mb-1">Ajan Says</h4>
                    <h3 className="mb-0">
                      {team.agents?.length || 0}
                    </h3>
                  </div>
                </Col>
                <Col md={4}>
                  <div className="border rounded p-3 text-center">
                    <h4 className="h6 text-muted mb-1">Grev Says</h4>
                    <h3 className="mb-0">
                      {team.tasks?.length || 0}
                    </h3>
                  </div>
                </Col>
                <Col md={4}>
                  <div className="border rounded p-3 text-center">
                    <h4 className="h6 text-muted mb-1">Oluturulma Tarihi</h4>
                    <h3 className="h5 mb-0">
                      {team.created_at ? new Date(team.created_at).toLocaleDateString() : 'Bilinmiyor'}
                    </h3>
                  </div>
                </Col>
              </Row>
            </Card.Body>
          </Card>
          
          <Tabs
            activeKey={activeTab}
            onSelect={handleTabChange}
            className="mb-4"
            fill
          >
            <Tab eventKey="overview" title={<><i className="bi bi-house me-2"></i>Genel Bak</>}>
              <Row>
                <Col md={8}>
                  <Card className="shadow-sm mb-4">
                    <Card.Header className="bg-white">
                      <h5 className="mb-0">Takm Bilgileri</h5>
                    </Card.Header>
                    <Card.Body>
                      <table className="table table-hover">
                        <tbody>
                          <tr>
                            <th style={{width: '150px'}}>Takm Ad</th>
                            <td>{team.name}</td>
                          </tr>
                          <tr>
                            <th>Aklama</th>
                            <td>{team.description || 'Aklama bulunmuyor'}</td>
                          </tr>
                          <tr>
                            <th>ID</th>
                            <td><code>{team.id}</code></td>
                          </tr>
                          <tr>
                            <th>Oluturulma</th>
                            <td>{team.created_at ? new Date(team.created_at).toLocaleString() : 'Bilinmiyor'}</td>
                          </tr>
                          <tr>
                            <th>Son Gncelleme</th>
                            <td>{team.updated_at ? new Date(team.updated_at).toLocaleString() : 'Bilinmiyor'}</td>
                          </tr>
                        </tbody>
                      </table>
                    </Card.Body>
                  </Card>
                </Col>
                <Col md={4}>
                  <Card className="shadow-sm mb-4">
                    <Card.Header className="bg-white">
                      <h5 className="mb-0">lemler</h5>
                    </Card.Header>
                    <Card.Body>
                      <div className="d-grid gap-2">
                        <Button variant="outline-primary">
                          <i className="bi bi-person-plus me-2"></i>
                          Ajan Ekle
                        </Button>
                        <Button variant="outline-success">
                          <i className="bi bi-plus-circle me-2"></i>
                          Grev Olutur
                        </Button>
                        <Button variant="outline-danger">
                          <i className="bi bi-trash me-2"></i>
                          Takm Sil
                        </Button>
                      </div>
                    </Card.Body>
                  </Card>
                  
                  {/* Gnlk */}
                  <Card className="shadow-sm">
                    <Card.Header className="bg-white">
                      <h5 className="mb-0">lem Gnl</h5>
                    </Card.Header>
                    <div style={{maxHeight: '300px', overflowY: 'auto'}}>
                      <ul className="list-group list-group-flush">
                        {logs.slice(-5).reverse().map(log => (
                          <li key={log.id} className="list-group-item">
                            <small className="d-block text-muted">{log.timestamp}</small>
                            <span className={`badge bg-${log.type === 'error' ? 'danger' : log.type === 'warning' ? 'warning' : log.type === 'success' ? 'success' : 'info'} me-2`}>
                              {log.type}
                            </span>
                            {log.message}
                          </li>
                        ))}
                        {logs.length === 0 && (
                          <li className="list-group-item text-center text-muted">
                            Henz ilem kayd bulunmuyor
                          </li>
                        )}
                      </ul>
                    </div>
                  </Card>
                </Col>
              </Row>
            </Tab>
            
            <Tab eventKey="agents" title={<><i className="bi bi-people me-2"></i>Ajanlar ({team.agents?.length || 0})</>}>
              <Agents teamId={team.id} agents={team.agents || []} />
            </Tab>
            
            <Tab eventKey="tasks" title={<><i className="bi bi-list-check me-2"></i>Grevler ({team.tasks?.length || 0})</>}>
              <TaskList teamId={team.id} tasks={team.tasks || []} />
            </Tab>
            
            <Tab eventKey="logs" title={<><i className="bi bi-journal-text me-2"></i>Gnlk ({logs.length})</>}>
              <Card>
                <Card.Header className="bg-white d-flex justify-content-between align-items-center">
                  <h5 className="mb-0">lem Gnl</h5>
                  <Button variant="outline-secondary" size="sm" onClick={() => setLogs([])}>
                    <i className="bi bi-trash me-1"></i> Temizle
                  </Button>
                </Card.Header>
                <Card.Body className="p-0">
                  <div className="log-container bg-dark text-light p-3" style={{height: '400px', overflowY: 'auto'}}>
                    {logs.length > 0 ? (
                      logs.map(log => (
                        <div key={log.id} className={`log-entry log-${log.type} mb-2`}>
                          <span className="log-time text-muted">[{log.timestamp}]</span>
                          <span className={`log-badge badge bg-${log.type === 'error' ? 'danger' : log.type === 'warning' ? 'warning' : log.type === 'success' ? 'success' : 'info'} mx-2`}>
                            {log.type.toUpperCase()}
                          </span>
                          <span className="log-message">{log.message}</span>
                        </div>
                      ))
                    ) : (
                      <p className="text-muted text-center my-5">Log kayd bulunmuyor</p>
                    )}
                  </div>
                </Card.Body>
              </Card>
            </Tab>
          </Tabs>
        </>
      )}
    </Container>
  );
};

export default TeamDetail; 