import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-hot-toast';
import api, { createTeam } from '../services/api';

function Teams({ refreshTeams, isLoading }) {
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [newTeam, setNewTeam] = useState({
    name: '',
    description: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [localTeams, setLocalTeams] = useState([]);
  const [loading, setLoading] = useState(true);

  // Takmlar getir
  useEffect(() => {
    fetchTeamsFromApi();
  }, []);

  // API'den takmlar direkt getir
  const fetchTeamsFromApi = async () => {
    try {
      setLoading(true);
      const response = await api.get('/api/teams');
      if (response.data && response.data.teams) {
        setLocalTeams(response.data.teams);
      }
    } catch (error) {
      console.error('Takmlar direkt getirilirken hata:', error);
      toast.error('Takmlar yklenirken hata olutu. Ltfen sayfay yenileyin.');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewTeam({ ...newTeam, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!newTeam.name.trim()) {
      toast.error('Takm ad gereklidir!');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      console.log('Takm oluturma istei gnderiliyor:', newTeam);
      const createdTeam = await createTeam(newTeam);
      console.log('Oluturulan takm:', createdTeam);
      
      // Takmlar yeniden ykle
      await fetchTeamsFromApi();
      
      toast.success('Takm baaryla oluturuldu!');
      setNewTeam({ name: '', description: '' });
      setShowCreateForm(false);
    } catch (error) {
      console.error('Takm olutururken hata:', error);
      toast.error(`Takm oluturulamad: ${error.response?.data?.detail || error.message}`);
    } finally {
      setIsSubmitting(false);
    }
  };

  if (isLoading || loading) {
    return (
      <div className="d-flex justify-content-center py-5">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Ykleniyor...</span>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h1 className="h2">Takmlar</h1>
        <div>
          <button 
            className="btn btn-outline-secondary me-2"
            onClick={fetchTeamsFromApi}
          >
            <i className="bi bi-arrow-clockwise me-1"></i> Yenile
          </button>
          <button 
            className="btn btn-primary"
            onClick={() => setShowCreateForm(!showCreateForm)}
          >
            {showCreateForm ? (
              <>
                <i className="bi bi-x-circle me-1"></i> ptal
              </>
            ) : (
              <>
                <i className="bi bi-plus-circle me-1"></i> Yeni Takm Olutur
              </>
            )}
          </button>
        </div>
      </div>

      {showCreateForm && (
        <div className="card mb-4 shadow-sm">
          <div className="card-header">
            <h5 className="mb-0">Yeni Takm Olutur</h5>
          </div>
          <div className="card-body">
            <form onSubmit={handleSubmit}>
              <div className="mb-3">
                <label htmlFor="name" className="form-label">Takm Ad <span className="text-danger">*</span></label>
                <input
                  type="text"
                  className="form-control"
                  id="name"
                  name="name"
                  value={newTeam.name}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="mb-3">
                <label htmlFor="description" className="form-label">Aklama</label>
                <textarea
                  className="form-control"
                  id="description"
                  name="description"
                  value={newTeam.description}
                  onChange={handleInputChange}
                  rows="3"
                ></textarea>
              </div>
              <div className="d-flex justify-content-end">
                <button
                  type="button"
                  className="btn btn-outline-secondary me-2"
                  onClick={() => setShowCreateForm(false)}
                >
                  ptal
                </button>
                <button
                  type="submit"
                  className="btn btn-success"
                  disabled={isSubmitting}
                >
                  {isSubmitting ? (
                    <>
                      <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                      Oluturuluyor...
                    </>
                  ) : (
                    <><i className="bi bi-check-circle me-1"></i> Takm Olutur</>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {localTeams.length > 0 ? (
        <div className="row">
          {localTeams.map((team, index) => (
            <div className="col-md-4 mb-4" key={team.id || index}>
              <div className="card h-100 shadow-sm">
                <div className="card-body">
                  <h5 className="card-title">{team.name}</h5>
                  <p className="card-text text-muted">{team.description || 'Aklama bulunmamaktadr.'}</p>
                  <p>
                    <span className="badge bg-info me-2">{team.agents?.length || 0} Ajan</span>
                    <span className="badge bg-secondary">{team.tasks?.length || 0} Grev</span>
                  </p>
                </div>
                <div className="card-footer bg-white">
                  <Link to={`/teams/${team.id}`} className="btn btn-outline-primary btn-sm">
                    <i className="bi bi-eye me-1"></i> Detaylar Grntle
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="alert alert-info">
          <i className="bi bi-info-circle me-2"></i>
          Henz takm bulunmamaktadr. Yeni bir takm oluturmak iin yukardaki butonu kullanabilirsiniz.
        </div>
      )}
    </div>
  );
}

export default Teams; 