import axios from 'axios';
import { toast } from 'react-hot-toast';

// API'nin base URL'ini ortam deikeninden al veya varsaylan deeri kullan
const baseURL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8000';

// axios istemcisini olutur
const api = axios.create({
  baseURL: baseURL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 30000, // 30 saniye zaman am
});

// stek interceptor'u
api.interceptors.request.use(
  (config) => {
    // stek gnderilmeden nce buradan geecek
    console.log(`API istei gnderiliyor: ${config.method.toUpperCase()} ${config.url}`);
    return config;
  },
  (error) => {
    console.error('API istei gnderilirken hata:', error);
    return Promise.reject(error);
  }
);

// Yant interceptor'u
api.interceptors.response.use(
  (response) => {
    // Baarl yantlar buradan geecek
    console.log(`API yant alnd: ${response.config.method.toUpperCase()} ${response.config.url}`, response.status);
    return response;
  },
  (error) => {
    // Hata durumunda buradan geecek
    console.error('API yantnda hata:', error);
    
    // Hata mesajn hazrla
    let errorMessage = 'Sunucu ile iletiim kurulamad. Ltfen internet balantnz kontrol edin.';
    
    if (error.response) {
      // Sunucudan yant geldi ama hata kodu dnd
      const status = error.response.status;
      
      if (status === 404) {
        errorMessage = 'stenen kaynak bulunamad.';
      } else if (status === 401) {
        errorMessage = 'Oturum sresi doldu veya yetkiniz yok.';
      } else if (status === 403) {
        errorMessage = 'Bu ilem iin yetkiniz bulunmuyor.';
      } else if (status === 500) {
        errorMessage = 'Sunucu hatas olutu. Ltfen daha sonra tekrar deneyin.';
      } else {
        // Sunucudan gelen hata mesajn kullan
        errorMessage = error.response.data?.detail || 
                       error.response.data?.message || 
                       `Hata kodu: ${status}`;
      }
    } else if (error.request) {
      // stek gnderildi ama yant alnamad
      if (error.code === 'ECONNABORTED') {
        errorMessage = 'stek zaman amna urad. Ltfen daha sonra tekrar deneyin.';
      }
    }
    
    // Toast ile hata bildirimini gster (non-blocking)
    if (!error.config?.skipErrorToast) {
      toast.error(errorMessage);
    }
    
    return Promise.reject(error);
  }
);

// API levleri
export const createTeam = async (teamData) => {
  try {
    console.log('Takm oluturma istei:', teamData);
    const response = await api.post('/api/teams/create', teamData);
    console.log('Takm oluturma yant:', response.data);
    toast.success('Takm baaryla oluturuldu');
    return response.data;
  } catch (error) {
    console.error('Takm oluturulurken hata:', error);
    throw error;
  }
};

export const addAgentToTeam = async (teamId, agentData) => {
  try {
    console.log(`${teamId} ID'li takma ajan ekleme istei:`, agentData);
    const response = await api.post(`/api/teams/${teamId}/agents/add`, agentData);
    console.log('Ajan ekleme yant:', response.data);
    toast.success('Ajan baaryla eklendi');
    return response.data;
  } catch (error) {
    console.error('Ajan eklenirken hata:', error);
    throw error;
  }
};

export const fetchTeams = async () => {
  try {
    console.log('Takmlar getiriliyor...');
    const response = await api.get('/api/teams');
    console.log('Takmlar alnd:', response.data.teams);
    return response.data.teams;
  } catch (error) {
    console.error('Takmlar getirilirken hata:', error);
    return [];
  }
};

export const fetchTasksForTeam = async (teamId) => {
  try {
    console.log(`${teamId} ID'li takmn grevleri getiriliyor...`);
    const response = await api.get(`/api/teams/${teamId}/tasks`);
    console.log('Takm grevleri alnd:', response.data.tasks);
    return response.data.tasks;
  } catch (error) {
    console.error('Takm grevleri getirilirken hata:', error);
    return [];
  }
};

export default api; 